<template>
<div id="app" @drop.prevent="onDrop" @dragenter="onDragEnter" @dragover="onDragOver" @dragleave="onDragLeave" :class="{active: dragging == true}">
  <image-elem :src="src"/>
  <router-view/>
</div>
</template>

<script>
import Image from '@/components/Image';

const imgSrc = require('@/assets/logo.png');

export default {
  name: 'App',
  components: {
    'image-elem': Image
  },
  data() {
    return {
      uploadedFiles: [],
      dragging: false,
      src: imgSrc
    }
  },
  methods: {
    onDrop(e) {
      let droppedFiles = [];
      let storage = window.localStorage;

      for (var f=0; f<e.dataTransfer.files.length; f++) {
        droppedFiles.push(e.dataTransfer.files[f]);
      }

      console.log(droppedFiles)

      if (storage.getItem('dropZone') === null) {
        storage.setItem('dropZone', JSON.stringify(droppedFiles));
      } else {
        let prevFiles = JSON.parse(storage.getItem('dropZone'));
        droppedFiles.forEach(file => {
          prevFiles.push(file);
        })

        storage.setItem('dropZone', JSON.stringify(prevFiles));
      }

      e.preventDefault();
      this.dragging = false;

      // this.uploadedFiles.push(e.dataTransfer.files);
    },

    onDragEnter(e) {
      console.log(e.dataTransfer.files);
      this.dragging = true;
      e.preventDefault();
    },
    onDragLeave(e) {
      e.preventDefault();
      this.dragging = false;
    },
    onDragOver(e) {
      e.preventDefault();
    },
  },

  mounted() {

  }

};
</script>

<style>
body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#app {
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  transition: 0.2s all ease-in-out;
}

.active {
  box-shadow: inset 0 0 0 10px rebeccapurple;
}
</style>
